.addNote {
  padding: 20px 0 20px 32px;
  font-family: 'Lato', sans-serif;
  font-weight: 500;
  font-size: 16px;
  line-height: 19px;
  letter-spacing: 0.15px;
  border: none;
  background: transparent;
  outline: none;
  color: #B4B4B4;
  position: relative;
  cursor: pointer;
  &::before {
    content: '';
    width: 14px;
    height: 14px;
    background-image: url("../../images/add-note.svg");
    left: 0;
    position: absolute;
    background-position: center;
    background-repeat: no-repeat;
    top: 50%;
    transform: translateY(-50%);

  }
}

.addNoteInput {
  margin-top: 20px;
  display: block;
  width: 100%;
  height: 44px;
  font-family: 'Lato', sans-serif;
  font-style: normal;
  font-weight: 600;
  font-size: 14px;
  line-height: 17px;
  letter-spacing: 0.15px;
  background: #FFFFFF;
  border: 1px solid #EFEFEF;
  border-radius: 4px;
  padding-left: 14px;
  padding-right: 14px;
  color: #C7C7C7;
  margin-bottom: 15px;
  transition: border-color .3s ease;
  &:focus {
    border-color: #e2dbdb;
    color: #000;
    outline: none;
  }
  &::placeholder {
    color: #C7C7C7;
    font-weight: 600;
    font-family: 'Lato', sans-serif;
  }
}

.addNoteBtnContainer {
  display: flex;
  gap: 0 10px;
}

.addNoteBtn {
  height: 40px;
  border-radius: 4px;
  border: none;
  font-family: 'Lato', sans-serif;
  font-style: normal;
  font-weight: 600;
  font-size: 14px;
  line-height: 17px;
  text-align: center;
  letter-spacing: 0.15px;
  cursor: pointer;
  padding: 0 15px;
  &:first-child {
    background-color: #4DD599;
    color: #fff;
  }
  &:nth-child(2) {
    background-color: #F4F6F8;
    color: #9C9C9C;
  }
}