.button {
  font-size: 16px;
  font-weight: 600;
  font-family: 'Lato', sans-serif;
  width: 100%;
  cursor: pointer;
  border-width: 0;
  background: none;
  padding: 10px 13px;
  align-items: center;
  display: flex;
  gap: 0 10px;
  position: relative;
  &_active {
    background: #FFFFFF;
    box-shadow: 0px 2px 10px rgba(0, 0, 0, 0.03);
    border-radius: 4px;
  }
  &:hover .closeBtn {
    transform: translateY(0);
    opacity: 1;
  }
}

.colorIndicator {
  width: 10px;
  height: 10px;
  border-radius: 50%;
}

.closeBtn {
  width: 10px;
  height: 10px;
  background-image: url('../../images/close.svg');
  margin-left: auto;
  transform: translateY(-10px);
  opacity: 0;
  transition: all .2s ease;
}

.name {
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
  max-width: 115px;
}